<template>
    <div>
        <div class="header"><h2>请输入企业三要素</h2></div>
        <div class="tableContainer">
            <div class="tableRow">
                <p>公司名称</p>
                <p><input type="text" v-model="checkParam.name"></p>
            </div>
            <div class="tableRow">
                <p>注册号</p>
                <p><input type="text" v-model="checkParam.code"></p>
            </div>
            <div class="tableRow">
                <p>公司法人</p>
                <p><input type="text" v-model="checkParam.legalPersonName"></p>
            </div>
        </div>
      <div class="footer" style="float: left;margin-left: 45%;margin-top: 50px"><input type="button" id="act" @click="check" value="查验"></div>
<!--      <div class="footer" style="float: right;margin-right: 500px;margin-top: 50px"><input type="button" @click="checkRecord" value="查验记录"></div>-->
    </div>
</template>
<script>
export default {
  name: 'enterpriseCheck',
  data: function () {
    return {
      act: '查验',
      checkParam: {
        // ruleList:[],
        // information:[],
        name: null,
        code: null,
        legalPersonName: null,
        reportId: null
      }
    }
  },
        // 解决路由传参不刷新问题
  activated: function () {
    this.checkParam.reportId = this.$route.query.reportId;
    // console.log(this.checkParam.reportId)
    // if(!this.$route.query.rules1&&!this.$route.query.rules2){
    //      this.$router.push({ path: '/rulesCreated'});
    //  }
    //  this.checkParam.ruleList = JSON.parse(this.$route.query.rules1);
    //  this.checkParam.information = JSON.parse(this.$route.query.rules2);


  },
  methods: {
    check() {
      // console.log(123);
      let _this = this;
      let checkParam = _this.checkParam;
      if (this.checkParam.name && this.checkParam.code && this.checkParam.legalPersonName) {

        let enRule = JSON.parse(window.localStorage.getItem("enRule"));
        let enInfor = JSON.parse(window.localStorage.getItem("enInfor"));
        _this.infor = enInfor;
        // console.log(enInfor)

        for (let i = 0; i < enRule.length; i++) {
          enRule[i].selected = true;
        }
        checkParam.ruleList = enRule;
        checkParam.information = enInfor;
        // checkParam.reportId
        // console.log(checkParam);
        //请求数据

        // $("#act").attr("value","值");
        document.getElementById('act').value = "查验中..."
        _this.checkParam = checkParam;
        // console.log(checkParam)
        _this.$request({
          url: '/check/rule',
          method: "post",
          data: checkParam
        }).then(response => {
          if (response.code === 200) {
            let data = response.data;
            // console.log(data);
            _this.$router.push({
              path: '/report/enterprise',
              query: {
                // checkParam: JSON.stringify(_this.checkParam),
                checkParam: JSON.stringify(data)
              },
            });
          } else {
            _this.msgError("服务器异常")
          }
          document.getElementById('act').value = "查验"
        }).catch(error => {
          document.getElementById('act').value = "查验"
        });


      } else {
        let _this = this;
        _this.msgError("查询内容不能为空");
      }
    },

    // checkRecord: function () {
    //   let _this = this;
    //   console.log(123);
    //   _this.$router.push({
    //     path: '/checkList'
    //   });
    // },
  },
}
</script>
<style scoped>
    .header{
        text-align: center;
    }
    .tableContainer{
        margin-left: auto;
        margin-right: auto;
        display: table;
        border-spacing: 20px;
    }
    .tableRow{
        display: table-row;
    }
    .tableRow p{
        display: table-cell;
        /*vertical-align: top;*/
    }
    .tableRow p:first-child{
        text-align: right;
        font-size: 20px;
        padding: 5px;
    }
    .tableRow p:nth-child(2) input{
        width: 200px;
        height: 18px;
        padding: 5px;
    }
    .footer{
        text-align: center;
    }
    .footer input{
        width: 200px;
        height: 35px;
        background-color: #0c4469;
        color: white;
        letter-spacing: 3px;
        font-size: 18px;
        border-radius: 5px;
    }
</style>



